.App {
  min-height: 100vh;
  width: 100vw;
  background: linear-gradient(
    to bottom,
    rgb(255, 255, 255),
    rgb(176, 178, 199)
  );
  background-image: url("https://verbclicovin.ml/wp-content/uploads/2020/03/Corona.png");
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-position: center;
}

#title {
  position: absolute;
  top: 0;
  left: 0;
  font-size: xx-large;
  font-weight: bolder;
  text-shadow: 1px 2px 1px gray;
}
